import React, { Component } from 'react';
import './App.css';
import { FormGroup, FormControl, InputGroup, Glyphicon } from 'react-bootstrap';
import axios from 'axios';
import Profile from './Profile';
import Gallery from './Gallery';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      query: '',
      artist: null,
      tracks: null
    };
  }

  getTokenFromURL = () => {
    const url = window.location.href;
    let access_token = url.match(/#(?:access_token)=([\S\s]*)/);
    if (access_token != null) access_token = access_token[1];
    console.log('url', url, 'access_token', access_token);
    return access_token;
  };

  searchTracks(FETCH_URL, AuthStr) {
    axios
      .get(FETCH_URL, { headers: { Authorization: AuthStr } })
      .then(response => {
        console.log("artist's top tracks:", response.data.tracks);
        const { tracks } = response.data;
        this.setState({ tracks });
        console.log('artist', this.state.artist, 'tracks', this.state.tracks);
      })
      .catch(error => console.log('error', error));
  }

  search() {
    const FETCH_URL_ARTIST = `https://api.spotify.com/v1/search?q=${this.state.query}&type=artist&limit=1`;
    const USER_TOKEN = this.getTokenFromURL();
    const AuthStr = `Bearer ${USER_TOKEN}`;
    /* get artist information */
    axios
      .get(FETCH_URL_ARTIST, { headers: { Authorization: AuthStr } })
      .then(response => {
        const artist = response.data.artists.items[0];        
        this.setState({ artist });
        /* get artist top-tracks after having artist.id */
        if (artist !== undefined) {          
          const FETCH_URL_TRACKS = `https://api.spotify.com/v1/artists/${artist.id}/top-tracks?country=US`;
          this.searchTracks(FETCH_URL_TRACKS, AuthStr);
        } else {
          this.setState({tracks: null})
        }
      })
      .catch(error => console.log('error', error));
  }

  render() {
    return (
      <div className="App">
        <a href="/login" className="btn btn-primary">
          Log in with Spotify
        </a>

        <div className="App-title">Music master</div>

        {/* search bar */}
        <FormGroup>
          <InputGroup>
            <FormControl
              className="App-searchbox"
              type="text"
              placeholder="Nhập tên nghệ sĩ"
              // query={this.state.query}
              onChange={event => this.setState({ query: event.target.value })}
              onKeyPress={event => {
                if (event.key === 'Enter') this.search();
              }}
            />
            <InputGroup.Addon onClick={() => this.search()}>
              <Glyphicon glyph="search" />
            </InputGroup.Addon>
          </InputGroup>
        </FormGroup>

        {this.state.artist !== null ? (
          <div>
            <Profile artist={this.state.artist} />
            <Gallery tracks={this.state.tracks} />
          </div>
        ) : (
          <div />
        )}
      </div>
    );
  }
}

export default App;



